<template>
  <div id="add">
    <div class="add-list">
      <h1>基本信息:</h1>

      <ul class="input-list">
        <li>
          <span>序号:</span>
          <input
            type="text"
            value=""
            placeholder="请输入商品的序号"
            v-model="goodsId"
            class="int" />
        </li>
        <li>
          <span>商品名称:</span>
          <input
            type="text"
            value=""
            placeholder="请输入商品的名称"
            v-model="goodsName"
            class="int"/>
        </li>
        <li>
          <span>价格:</span>
          <input
            type="text"
            value=""
            placeholder="请输入商品的价格"
            v-model="goodsPrice"
            class="int"/>
        </li>

        <li>
          <span>分类:</span>
          <select name="" id="" v-model="selected" class="select">
            <option v-for="(item, index) in options" :key="index">
              {{ item.value }}
            </option>
          </select>
        </li>
      </ul>
      <button @click="addAction" class="btn">添加</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      goodsId: "",
      goodsName: "",
      goodsPrice: "",
      selected: "新品专区",
      options: [
        { value: "推荐专区" },
        { value: "新品专区" },
        { value: "居家生活" },
        { value: "服饰鞋包" },
        { value: "美食酒水" },
        { value: "个人清洁" },
        { value: "母婴亲子" },
        { value: "运动旅行" },
        { value: "数码家电" },
      ],
    };
  },
  methods: {
    addAction() {
      this.$store.dispatch("goods/requestAdd", {
        goodsId: this.goodsId,
        goodsName: this.goodsName,
        goodsPrice: this.goodsPrice,
        category: this.selected,
      });
      this.$router.push({name:'goods-list'});
    },
  },
};
</script>

<style scoped lang="scss">
.add-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  // align-items: center;
  justify-content: flex-start;
  h1{
    height: 30px;
    font-size: 20px;
    line-height: 30px;
    text-align: left;
    font-weight: 700;
  }
  .input-list{
    display: flex;
    
    justify-content: flex-start;
    // align-items: center;
    flex-direction: column;
    li{
      height: 30px;
      line-height: 30px;
      display: flex;
      width: 20%;
      justify-content: space-between;
      span{
        font-size: 14px;
        font-weight: 700;
        color: #333;
        padding: 0 8px;

      }
      .int{
        height: 24px;
        border-radius: 4px;
        padding: 2px 6px;
        font-size: 8px;
        color: #333;
      }
      .select{
        width: 62%;
        border-radius: 3px;
      }

    }
  }
  .btn{
    width: 60px;
    height: 30px;
    border-radius:4px ;
    font-size: 14px;
    font-weight: 700;
    color: #333;
    margin-left: 100px;
  }


 
}
</style>